.App {
	text-align: center;
    background-color: #e3e3e3;
    overflow: auto;
}

.heading-title {
    font-weight: bold;
    display: inline-block;
    padding-bottom: 5px;
	position: relative;
}

.heading-title:before{
    content: "";
    position: absolute;
    width: 70%;
    height: 1px;
    bottom: 0;
	left: 15%;
	border-radius: 2px;
    border-bottom: 4px solid black;
}