#sidebar-main {
    padding: 60px 60px;
    color: white;
}

.sidebar-dark {
    background-color: #02262e;
    color: white;
}

.side-block {
    padding: 40px 0;
}

.side-subheader {
    text-align: left;
    margin-top: 20px;
    color: #abc4bc;
}

.side-category {
    margin: 0;
    padding: 0;
    list-style-type: none;
    text-align: left;
}

.side-category li {
    display: table;
    padding: 5px 20px;
    font-size: 95%;
    color: #a6e0ff;
}

.side-category li p {
    display: block;
}

.show-lg {
    display: block;
}

.show-sm {
    display: none;
}

@media screen and (max-width: 992px) {
    .side-category {
        list-style-type: none;
        text-align: center;
    }
    .side-subheader {
        text-align: center;
    }
    .side-category>li {
        display: inline-block;
        border: 1px solid #abc4bc;
        border-radius: 20px;
        margin: 5px;
        padding: 10px;
        font-size: 90%;
        color: white;
    }
    .show-lg {
        display: none;
    }
    .show-sm {
        display: block;
    }
}