#landing-main {
    margin: 0;
    height: 100vh;
    width: 100vw;
    max-width: 100%;
    background-color: #00151a;
    font-size: 200%;
    color: white;
}

a {
    color: white !important;
}

a:hover {
    color: #34eb92 !important;
}

a:active {
    color: #67b5a4 !important;
}

#landing-icons{
    position: absolute;
    bottom: 30px;
}

.center-holder {
    position: absolute;
    z-index: 1000;
}

#landing-main .name {
    color: #34eb92;
}

.vertical-center {
    min-height: 100%;
    min-height: 100vh;

    display: flex;
    align-items: center;
}

.landing-btn {
    margin: 10px;
    padding: 10px 20px;
    border: 2px solid white;
    
    background: transparent;

    font-size: 65%;
    color: white;
}

.landing-btn:hover {
    border: 2px solid #67b5a4;
    background-color: #67b5a4;
    color: white;
    text-decoration: none;
}

#landing-text {
    position: relative !important;
}

.landing-icon {
    margin: 10px;
}

.icon:hover path {
    fill: #67b5a4;
}

.icon {
    display: inline;
}

.icon:hover {
    
    cursor: pointer;
}

@media screen and (max-width: 1200) {

    svg {
        width: 40px;
    }

    #root {
        font-size: 90%;
    }
}

@media screen and (max-width: 992px) {
    svg {
        width: 37px;
    }
    #root {
        font-size: 80%;
    }
}

@media screen and (max-width: 768px) {
    svg {
        width: 35px;
    }
    #root {
        font-size: 73%;
    }
}

@media screen and (max-width: 608px) {
    svg {
        width: 32px;
    }
    #root {
        font-size: 70%;
    }
}