#about-main {
    padding: 50px 50px 0px 50px;
    overflow: hidden;
}

.about-photo {
    border-radius: 50%;
    margin: 20px auto;
    height: 40%;
    width: 40%;
    min-width: 120px;
}

.content-panel {
    padding: 30px 20px;
    background-color: white;
    display: table;
    width: 100%;
    margin: 0px auto;
}

#about-text {
    padding: 0 20%;
}

@media screen and (max-width: 1200) {
    #about-main {
        padding: 40px 40px 0px 40px;
    }
}

@media screen and (max-width: 992px) {
    #about-main {
        padding: 30px 30px 0px 30px;
        overflow-x: hidden;
        overflow: visible;
    }
}

@media screen and (max-width: 768px) {
    #about-main {
        padding: 10px 0px 0px 0px;
    }
}

@media screen and (max-width: 608px) {

    .content-panel {
        width: 100%;
    }
}